import React from 'react';
import { graphql, HeadProps, PageProps } from 'gatsby';
import { getSrc } from 'gatsby-plugin-image';
import BookPage from 'containers/BookPage';
import { BookPageContext } from 'models/book';
import SEO from 'components/SEO';
import { getAlternateLanguages } from 'utils/i18nPath';

const BookTemplate = (props: PageProps<Queries.BookPageQuery, BookPageContext>) => {
  return <BookPage {...props} />;
};

export default BookTemplate;

export const Head = ({ data, pageContext, location }: HeadProps<Queries.BookPageQuery, BookPageContext>) => {
  const thumbnail = data.markdownRemark?.frontmatter?.thumbnail?.childImageSharp;
  const thumbnailUrl = thumbnail != null ? getSrc(thumbnail) : undefined;
  const slug = data.markdownRemark?.fields?.slug ?? '';

  const alternateLanguages = getAlternateLanguages(pageContext.lang, `/books/${slug}/`);

  return (
    <SEO
      title={data.markdownRemark?.frontmatter?.title ?? undefined}
      description={data.markdownRemark?.frontmatter?.description ?? undefined}
      image={thumbnailUrl}
      lang={pageContext.lang}
      pathname={location.pathname}
      alternateLanguages={alternateLanguages}
      includeXDefaultAlternate={true}
    />
  );
};

export const pageQuery = graphql`
  query BookPage($slug: String!, $lang: String!) {
    markdownRemark(fields: { slug: { eq: $slug }, mdType: { eq: "books" }, lang: { eq: $lang } }) {
      ...Book
    }
    allMarkdownRemark(
      filter: { fields: { mdType: { eq: "posts" } } }
      sort: { frontmatter: { date: ASC } }
    ) {
      edges {
        node {
          fields {
            path
          }
          frontmatter {
            title
            subTitle
            date(formatString: "MMM DD, YYYY")
          }
        }
      }
    }
  }
`;
