import React, { useMemo } from 'react';
import { Box, Container, Divider, Typography, useTheme } from '@mui/material';
import { GatsbyImage } from 'gatsby-plugin-image';
import { Link, type PageProps } from 'gatsby';
import Layout from 'components/Layout';

const BookPage = ({ data }: PageProps<Queries.BookPageQuery>) => {
  const theme = useTheme();
  const book = data.markdownRemark;
  const allPosts = data.allMarkdownRemark.edges;

  const postPaths = book?.frontmatter?.posts ?? [];

  const chapters = useMemo(() => {
    return postPaths
      .map(postPath => {
        const matched = allPosts.find(({ node }) => node.fields?.path === postPath);
        if (matched == null) return null;
        return {
          path: matched.node.fields?.path ?? '',
          title: matched.node.frontmatter?.title ?? '',
          subTitle: matched.node.frontmatter?.subTitle ?? '',
          date: matched.node.frontmatter?.date ?? '',
        };
      })
      .filter(Boolean) as Array<{ path: string; title: string; subTitle: string; date: string }>;
  }, [postPaths, allPosts]);

  if (book == null) return null;

  const title = book.frontmatter?.title ?? '';
  const subTitle = book.frontmatter?.subTitle ?? '';
  const description = book.frontmatter?.description ?? '';
  const author = book.frontmatter?.author ?? '';
  const thumbnail = book.frontmatter?.thumbnail?.childImageSharp?.gatsbyImageData;

  return (
    <Layout>
      {/* Book cover + info */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: { xs: 'column', sm: 'row' },
          gap: { xs: theme.spacing(3), sm: theme.spacing(5) },
          alignItems: { xs: 'center', sm: 'flex-start' },
          marginBottom: theme.spacing(5),
        }}
      >
        {/* 3D Book cover */}
        {thumbnail && (
          <Box
            sx={{
              flexShrink: 0,
              width: { xs: 160, sm: 180 },
              perspective: '600px',
            }}
          >
            <Box
              sx={{
                position: 'relative',
                transformStyle: 'preserve-3d',
                transform: 'rotateY(-8deg)',
                transition: 'transform 0.3s ease',
                '&:hover': {
                  transform: 'rotateY(-2deg)',
                },
              }}
            >
              {/* Cover */}
              <Box
                sx={{
                  borderRadius: '2px 8px 8px 2px',
                  overflow: 'hidden',
                  boxShadow:
                    '6px 6px 18px -2px rgba(0, 0, 0, 0.2), 1px 1px 3px rgba(0, 0, 0, 0.1)',
                }}
              >
                <GatsbyImage image={thumbnail} alt={title} />
              </Box>
              {/* Spine edge */}
              <Box
                sx={[
                  {
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: 12,
                    height: '100%',
                    transformOrigin: 'left center',
                    transform: 'rotateY(90deg) translateX(-6px)',
                    background: 'linear-gradient(to right, #d4d0c8, #e8e4dc)',
                    borderRadius: '2px 0 0 2px',
                  },
                  t =>
                    t.applyStyles('dark', {
                      background: 'linear-gradient(to right, #3a3a3a, #4a4a4a)',
                    }),
                ]}
              />
            </Box>
          </Box>
        )}

        {/* Book info */}
        <Box sx={{ textAlign: { xs: 'center', sm: 'left' }, flex: 1 }}>
          <Typography
            variant="h4"
            component="h1"
            fontWeight="bold"
            lineHeight={1.3}
            letterSpacing="-0.02em"
            sx={{
              marginBottom: theme.spacing(1),
              fontSize: { xs: '1.5rem', sm: '1.75rem', md: '2rem' },
            }}
          >
            {title}
          </Typography>

          {subTitle && (
            <Typography
              variant="body1"
              color="text.secondary"
              lineHeight={1.6}
              sx={{
                marginBottom: theme.spacing(1.5),
                fontSize: { xs: '0.92rem', sm: '1rem' },
              }}
            >
              {subTitle}
            </Typography>
          )}

          {author && (
            <Typography variant="body2" color="text.secondary" sx={{ marginBottom: theme.spacing(2) }}>
              {author}
            </Typography>
          )}

          <Typography
            variant="caption"
            color="text.secondary"
            sx={{ fontSize: '0.82rem' }}
          >
            {chapters.length} chapters
          </Typography>
        </Box>
      </Box>

      {/* Description */}
      {description && (
        <Box sx={{ marginBottom: theme.spacing(4) }}>
          <Typography
            variant="body1"
            lineHeight={1.9}
            color="text.primary"
            fontFamily={`"Pretendard Variable", Pretendard, "Helvetica Neue", Arial, sans-serif`}
          >
            {description}
          </Typography>
        </Box>
      )}

      <Divider sx={{ marginBottom: theme.spacing(3) }} />

      {/* TOC */}
      <Box component="ol" sx={{ padding: 0, margin: 0, listStyle: 'none' }}>
        {chapters.map((chapter, index) => (
          <Box
            component="li"
            key={chapter.path}
            sx={{
              borderBottom: `1px solid ${theme.palette.divider}`,
            }}
          >
            <Link
              to={chapter.path}
              style={{
                textDecoration: 'none',
                display: 'flex',
                alignItems: 'baseline',
                gap: theme.spacing(2),
                padding: `${theme.spacing(2.5)} ${theme.spacing(1)}`,
                color: theme.palette.text.primary,
                transition: 'background-color 0.2s ease-in-out',
              }}
            >
              <Typography
                variant="body2"
                color="text.secondary"
                sx={{ flexShrink: 0, minWidth: 24, fontVariantNumeric: 'tabular-nums' }}
              >
                {String(index + 1).padStart(2, '0')}
              </Typography>
              <Box>
                <Typography
                  variant="body1"
                  fontWeight="medium"
                  lineHeight={1.5}
                  sx={{ marginBottom: chapter.subTitle ? theme.spacing(0.5) : 0 }}
                >
                  {chapter.title}
                </Typography>
                {chapter.subTitle && (
                  <Typography variant="body2" color="text.secondary" lineHeight={1.5}>
                    {chapter.subTitle}
                  </Typography>
                )}
              </Box>
            </Link>
          </Box>
        ))}
      </Box>
    </Layout>
  );
};

export default BookPage;
